:root {
  --inputs-background: #252525;
  --main-section-background: #181818;
  --box-shadow: 0 0 20px rgba(0, 0, 0, 0.438);
  --main-gradient: linear-gradient(to right, rgba(0, 242, 96, .5), rgba(5, 117, 230, .5));
  --radius: 10px
}

body {
  display: flex;
  justify-content: center;
  text-align: center;

  background-color: #171717;
  
  color: rgb(255, 255, 255);
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#area {
  display: flex;
}